!(function (window) {    /// = /    //a/750 = ?/100 => ? = a*100/750    //=>  html   fontSize     // 320<=  <= 420,     //1.iPhone5320x568640x1136@2x    //2.iPhone6375x667750x1334@2x    //iPhone6 Plus414x7361242x2208@3x    const doc = window.document,        docHtml = doc.documentElement,        UIwidth = 750,        resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize';    function setHtmlFontSize() {        let docHtmlWidth = docHtml.getBoundingClientRect().width; //        let fontSize = docHtmlWidth<320            ?320*100/UIwidth            :docHtmlWidth>420                ?420*100/UIwidth                :docHtmlWidth*100/UIwidth;        docHtml.style.fontSize = fontSize + 'px';    }    //init    setHtmlFontSize();    //add data-dpr    docHtml.setAttribute('data-dpr', window.navigator.appVersion.match(/iphone/gi) ? window.devicePixelRatio : 1);    if (!docHtml.addEventListener) return;    window.addEventListener(resizeEvt, setHtmlFontSize, false);    docHtml.addEventListener('DOMContentLoaded', setHtmlFontSize, false);})(window);


