import http from './../../http'
import Vue from 'vue'

//type 
const GETORDERLIST = 'GETORDERLIST'

const orderList = {
    state:{
        list:[],
        serviceList:[]
    },
    mutations:{
        //
        setList(state,list){
            state.list = list;
        },
        //
        changeStatus(state,id){
            let p  = [...state.list];
            p.map(item=>{
                if(item.id === id){
                    item.status = 99
                }
            })
            state.list = p;
        },
        //
        setServiceList(state,list){
            state.serviceList = list;
        },
        //
        repairStart(state,id){
            let p  = [...state.serviceList];
            p.map(item=>{
                if(item.id === id){
                    item.status = 2
                }
            })
            state.serviceList = p;
        },
        //
        repairEnd(state,id){
            let p  = [...state.serviceList];
            p.map(item=>{
                if(item.id === id){
                    item.status = 3
                }
            })
            state.serviceList = p;
        }
    },
    actions:{
    
    }
}

export default orderList;


